:root { --grid-column-count: 12; --grid-baseline: 16px; --grid-baseline-calc: unitless($grid-baseline); }

.grid { position: absolute; top: 0; left: 0; right: 0; bottom: 0; z-index: 10000; overflow: hidden; pointer-events: none; transform-origin: 50% 0%; }

.grid__container { flex-grow: 1; margin: 0 auto; padding-left: 30px; padding-right: 30px; max-width: 1320px; height: 100%; opacity: 0; transition: opacity 0.2s; }

@media (min-width: 420px) { .grid__container { max-width: 1350px; } }

@media (min-width: 720px) { .grid__container { padding-left: 50px; padding-right: 50px; max-width: 1390px; } }

@media (min-width: 1080px) { .grid__container { padding-left: 70px; padding-right: 70px; max-width: 1430px; } }

.grid.isVerticalVisible .grid__container { opacity: 1; }

.gridIsHorizontalIsVisible { background: linear-gradient(to bottom, rgba(0, 0, 0, 0) calc(100% - (1 / var(--grid-baseline-calc) * 100%)), rgba(0, 0, 0, 0.05) calc(100% - (1 / var(--grid-baseline-calc) * 100%))), linear-gradient(to bottom, rgba(0, 0, 0, 0) calc(100% - (1 / var(--grid-baseline-calc) * 100%)), rgba(255, 255, 255, 0.15) calc(100% - (1 / var(--grid-baseline-calc) * 100%))); background-size: var(--grid-baseline) var(--grid-baseline); }

.grid__row { display: flex; flex-wrap: wrap; align-items: stretch; justify-content: flex-start; margin-left: -15px; margin-right: -15px; flex-direction: row; height: 100%; }

.grid__column { flex: none; align-self: stretch; padding-left: 15px; padding-right: 15px; width: calc(100% / var(--grid-column-count, 12)); position: relative; height: 100%; }

.grid__visualize { position: relative; width: 100%; height: 100%; }

.grid__visualize::before, .grid__visualize::after { content: ''; display: block; width: 100%; height: 100%; background-color: rgba(255, 255, 255, 0.1); }

.grid__visualize::after { position: absolute; top: 0; left: 0; background-color: rgba(0, 0, 0, 0.05); }

.grid__button { position: fixed; right: 75px; top: 0; z-index: 10000; padding: 0 15px; height: 26px; border-style: solid; border-width: 0 1px 1px; border-color: rgba(0, 0, 0, 0.1); border-radius: 0 0 2px 2px; cursor: pointer; pointer-events: all; font-size: 12px; text-transform: uppercase; color: #999; background-color: #fff; opacity: 1; transition: opacity 0.25s, transform 0.1s; }

.grid__button:hover { color: #000; }

.grid__button:hover g { fill: #000; }

.grid__button:first-of-type { right: 131px; }

.grid__button__svg { margin-top: 4px; }

.grid__button__svg g { fill: #8d8d8d; }

.grid__button.horizontal g, .grid__button.horizontal:hover g, .grid__button.vertical g, .grid__button.vertical:hover g { fill: #328bf3; }

[data-columns='1'] .grid__column { min-width: 100%; }

[data-columns='2'] .grid__column { min-width: 50%; }

[data-columns='3'] .grid__column { min-width: 33.33333%; }

[data-columns='4'] .grid__column { min-width: 25%; }

[data-columns='5'] .grid__column { min-width: 20%; }

[data-columns='6'] .grid__column { min-width: 16.66667%; }

[data-columns='7'] .grid__column { min-width: 14.28571%; }

[data-columns='8'] .grid__column { min-width: 12.5%; }

[data-columns='9'] .grid__column { min-width: 11.11111%; }

[data-columns='10'] .grid__column { min-width: 10%; }

[data-columns='11'] .grid__column { min-width: 9.09091%; }

[data-columns='12'] .grid__column { min-width: 8.33333%; }
